}
:root {
    --gap-2xl: 32px;
}
:root {
    --border-radius-m: 8px;
}
:root {
    /* default */
    --toast-plate-border-radius: var(--border-radius-m);

    /* inverted */
}
:root {
    --toast-box-shadow: var(--shadow-m);
}
.toast__toastPlate_bp3xx {
    box-shadow: var(--toast-box-shadow);
    word-break: break-word;
}
.toast__title_bp3xx {
    font-weight: 400;
}
.toast__popoverInner_bp3xx {
    border: none;
    border-radius: var(--toast-plate-border-radius);
    box-shadow: var(--toast-box-shadow);
    background-color: transparent;
}
.toast__block_bp3xx {
    width: 100%;
}
.toast__fixed_bp3xx {
    position: fixed;
    bottom: var(--gap-2xl);
    transition: opacity 150ms ease-in-out, transform 150ms ease-in-out
}
.toast__fixed_bp3xx.toast__enter_bp3xx {
        opacity: 0;
        transform: scale(0.98);
    }
.toast__fixed_bp3xx.toast__enterActive_bp3xx {
        opacity: 1;
        transform: scale(1);
    }
.toast__fixed_bp3xx.toast__exit_bp3xx {
        opacity: 1;
        transform: scale(1);
    }
.toast__fixed_bp3xx.toast__exitActive_bp3xx {
        opacity: 0;
        transform: scale(0.98);
    }
