}
:root {
    --gap-3xs: 2px;
    --gap-2xs: 4px;
    --gap-xs: 8px;
    --gap-s: 12px;
    --gap-xl: 24px;
}
.file-upload-item__component_15mxo {
    font-size: 13px;
    line-height: 16px;
    font-weight: 400;

    display: flex;
    width: 100%;
    padding: var(--gap-xs);
    color: var(--color-light-text-secondary);
    box-shadow: 0 -1px 0 0 var(--color-light-border-primary) inset;
    box-sizing: border-box
}
.file-upload-item__component_15mxo svg {
        display: block;
    }
.file-upload-item__infoSection_15mxo {
    display: flex;
    flex-direction: column;
    flex: 1;
    margin-top: var(--gap-2xs);
    margin-left: var(--gap-s);
    min-width: 40%;
}
.file-upload-item__info_15mxo {
    display: flex;
    flex-wrap: wrap;
    flex: 1;
    overflow: hidden;
}
.file-upload-item__icon_15mxo {
    display: block;
    flex-shrink: 0;
    color: var(--color-light-text-primary);
}
.file-upload-item__errorIcon_15mxo {
    flex-shrink: 0;
    color: var(--color-light-graphic-negative);
}
.file-upload-item__successIcon_15mxo {
    flex-shrink: 0;
    color: var(--color-light-graphic-positive);
}
.file-upload-item__name_15mxo {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: var(--color-light-text-primary)
}
.file-upload-item__name_15mxo a {
        display: block;
        margin-bottom: 1px;
    }
.file-upload-item__meta_15mxo {
    display: flex;
    white-space: nowrap;
    margin-right: var(--gap-2xs);
    margin-left: var(--gap-xl);
    padding: var(--gap-2xs) 0
}
.file-upload-item__meta_15mxo > * {
        margin-right: var(--gap-s)
    }
.file-upload-item__meta_15mxo > *:last-child {
            margin-right: 0;
        }
.file-upload-item__size_15mxo {
    text-align: right;
}
.file-upload-item__delete_15mxo,
.file-upload-item__download_15mxo {
    height: 24px;
    width: 24px;
    margin-left: var(--gap-xs);
}
.file-upload-item__errorWrapper_15mxo {
    margin-top: var(--gap-s);
    color: var(--color-light-text-accent)
}
@media (--small-only) {
.file-upload-item__errorWrapper_15mxo {
        flex: 1 0 100%;
        margin-top: var(--gap-3xs)
}
    }
.file-upload-item__restore_15mxo {
    font-size: 14px;
    line-height: 20px;
    font-weight: 400;

    margin-left: var(--gap-s);
}
.file-upload-item__spinnerWrapper_15mxo {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 24px;
    height: 24px;
}
.file-upload-item__spinner_15mxo {
    width: 20px;
    height: 20px;
}
.file-upload-item__uploadPercent_15mxo {
    margin-top: var(--gap-2xs);
    margin-left: var(--gap-xl);
}
