    --color-light-graphic-accent: #ef3124;
    --color-light-text-secondary: rgba(11, 31, 53, 0.7);
    --color-light-graphic-accent-shade-7: rgb(222, 46, 33);
    --color-light-graphic-accent-shade-15: rgb(203, 42, 31);
