:root {
    --border-radius-m: 8px;
}
:root {
    --card-image-background: #e7e8ea;
    --card-image-border-radius: var(--border-radius-m);
}
.card-image__cardImage_orjby {
    position: relative;
    display: inline-block;
    background: var(--card-image-background);
    transition: background 0.2s ease;
}
.card-image__rounded_orjby {
    border-radius: var(--card-image-border-radius)
}
.card-image__rounded_orjby .card-image__image_orjby {
        border-radius: var(--card-image-border-radius);
    }
.card-image__image_orjby {
    display: block;
    max-width: 100%;
    height: auto;
    opacity: 0;
    transition: opacity 0.2s ease;
}
.card-image__loaded_orjby {
    background: transparent
}
.card-image__loaded_orjby .card-image__image_orjby {
        opacity: 1;
    }
