:root {
    --backdrop-visible-background: var(--color-light-bg-overlay);
    --backdrop-hidden-background: transparent;
}
:root {
    --drawer-width: 500px;
}
.drawer__component_2hxdu {
    position: fixed;
    top: 0;
    height: 100%;
    width: var(--drawer-width);
    flex: 1;
    overflow: auto;
    will-change: transform;
}
.drawer__rightPlacement_2hxdu {
    right: 0;
    align-self: flex-end;
}
.drawer__leftPlacement_2hxdu {
    left: 0;
    align-self: flex-start;
}
.drawer__content_2hxdu {
    width: 100%;
    display: flex;
    flex-direction: column;
    flex: 1;
}
.drawer__simplebar_2hxdu {
    height: 100vh;
}
/* enter */
.drawer__enterRight_2hxdu {
    transform: translateX(100%);
}
.drawer__enterLeft_2hxdu {
    transform: translateX(-100%);
}
.drawer__contentEnter_2hxdu {
    opacity: 0;
}
.drawer__backdropEnter_2hxdu {
    background-color: var(--backdrop-hidden-background);
}
.drawer__enterActive_2hxdu {
    transition: transform 0.3s ease-in-out;
    transform: translateX(0);
}
.drawer__backdropEnterActive_2hxdu,
.drawer__backdropEnterDone_2hxdu {
    transition: background 0.3s ease-in-out;
    background-color: var(--backdrop-visible-background);
}
.drawer__contentEnterActive_2hxdu {
    transition: opacity 0.2s ease-in-out 0.3s;
    opacity: 1;
}
/* exit */
.drawer__exit_2hxdu {
    transform: translateX(0);
}
.drawer__backdropExit_2hxdu {
    background-color: var(--backdrop-visible-background);
}
.drawer__contentExit_2hxdu {
    opacity: 1;
}
.drawer__exitActiveRight_2hxdu {
    transition: transform 0.25s ease-in-out 0.1s;
    transform: translateX(100%);
}
.drawer__exitActiveLeft_2hxdu {
    transition: transform 0.25s ease-in-out 0.1s;
    transform: translateX(-100%);
}
.drawer__backdropExitActive_2hxdu,
.drawer__backdropExitDone_2hxdu {
    transition: background 0.25s ease-in-out 0.1s;
    background-color: var(--backdrop-hidden-background);
}
.drawer__contentExitActive_2hxdu {
    opacity: 0;
    transition: opacity 0.12s ease-in-out;
}
